import { Component, OnInit, OnDestroy } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { FormGroup } from '@angular/forms';
import { MODULO, LENGUAJE, PAIS, MODAL } from 'src/app/modules/common/CONST';
import { Modal } from 'src/app/modules/common/components/modal/modal';
import { AlertModalTMAComponent } from 'src/app/modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';
import { InfoModalTMAComponent } from 'src/app/modules/common/components/entryComponents/info-modal-tma/info-modal-tma.component';
@Component({
  selector: 'app-aut002',
  templateUrl: './aut002.component.html',
  styleUrls: ['./aut002.component.scss', '../../../../../assets/cam/scss/components/autorizaciones/_aut002.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Aut002Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {
  contrato: {
    PA?: {
      MANIFESTACIONES_AUTORIZACIONES: {
        title: string;
        message: string;
        fecha: string;
        version: string;
      };
      DECLARACION_FLUJO_INGRESO: {
        title: string;
        message: string;
        fecha: string;
        version: string;
      };
    },
    CR?: { };
    SV?: { };
    HN?: { };
  };

  catalogoData: {
    CR?: {
      PAISES: Array<{ value: string; label: string; }>;
    };
    SV?: {
      PAISES: Array<{ value: string; label: string; }>;
    };
    HN?: {
      PAISES: Array<{ value: string; label: string; }>;
    };
    PA?: {
      PAISES: Array<{ value: string; label: string; }>;
    };
  };

  errores: {
    CR?: {
      medios: Array<{ type: string, label: string; }>;
      investigacionesJudicialesAdministrativas: Array<{ type: string, label: string; }>;
    };
    SV?: {
      medios: Array<{ type: string, label: string; }>;
      investigacionesJudicialesAdministrativas: Array<{ type: string, label: string; }>;
    };
    HN?: {
      medios: Array<{ type: string, label: string; }>;
      investigacionesJudicialesAdministrativas: Array<{ type: string, label: string; }>;
    };
    PA?: {
      medios: Array<{ type: string, label: string; }>;
      investigacionesJudicialesAdministrativas: Array<{ type: string, label: string; }>;
    };
  };

  textoContrato: string;
  ngOnInit() {
    this.setGeneralRules();
  }

  ngOnDestroy(): void {
    // throw new Error('Method not implemented.');
  }

  getDataToPayload(data?: any): () => ({ [key: string]: any; }) {
    this.disableButton = true;
    return () => this.getPayload();
  }

  verificarInvestigaciones() {
    const payload = this.formulario.getRawValue();
    if (payload.tieneInvestigacionesJudicialesAdministrativas) {
      this.catalogo.data('MSG_AUT002_001').subscribe((response: Array<Modal>) => {
        if ( !response ) {
          return;
        }
        this.obser.sendData({
          buttons: response[0].buttons.map( item => {
            const post: string = item.callback as any;
            item.buttonText = item.buttonText;
            item.callback = (close) => {
              this.postMessagesService.appFinish('' , post);
              close();
            };
            item.class = 'alertModal__footer--button alertModal__footer--button--primary';
            return item;
          }),
          title: response[0].title,
          message: response[0].message,
          display: true,
          entryComponent: AlertModalTMAComponent
        }, MODAL);
      });
      return;
    }
    this.onCall(this.getDataToPayload());
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  getPayload(): any {
    const payload = this.formulario.getRawValue();
    const datos = {
      aceptoManifestacionesAutorizaciones: {
        check: true,
        fecha: this.contrato.PA.MANIFESTACIONES_AUTORIZACIONES.fecha,
        version: this.contrato.PA.MANIFESTACIONES_AUTORIZACIONES.version,
      },
      declaroObligaciones: {
          check: true,
          fecha: this.contrato.PA.DECLARACION_FLUJO_INGRESO.fecha,
          version: this.contrato.PA.DECLARACION_FLUJO_INGRESO.version,
      },
        investigacionesJudicialesAdministrativas:
        payload.tieneInvestigacionesJudicialesAdministrativas ? payload.investigacionesJudicialesAdministrativas : '',
        medios: payload.medios,
        tieneInvestigacionesJudicialesAdministrativas: payload.tieneInvestigacionesJudicialesAdministrativas
    };
    return datos;
  }

  setGeneralRules(): void {
    this.contrato = {
      PA: {
        MANIFESTACIONES_AUTORIZACIONES: null,
        DECLARACION_FLUJO_INGRESO: null
      }
    };
    this.catalogoData = {
      PA: {
        PAISES: []
      },
      HN: {
        PAISES: []
      },
      CR: {
        PAISES: []
      },
      SV: {
        PAISES: []
      }
    };
    this.errores = {
      PA: {
        medios: [{ type: this.rules.errorTypeRequired, label: 'Informacin requerida' }],
        investigacionesJudicialesAdministrativas: [
          { type: this.rules.errorTypeSelect, label: 'Selecciona un elemento de la lista preaprobada.' }
        ]
      },
      CR: {
        medios: [{ type: this.rules.errorTypeRequired, label: 'Informacin requerida' }],
        investigacionesJudicialesAdministrativas: [
          { type: this.rules.errorTypeSelect, label: 'Selecciona un elemento de la lista preaprobada.' }
        ]
      },
      SV: {
        medios: [{ type: this.rules.errorTypeRequired, label: 'Informacin requerida' }],
        investigacionesJudicialesAdministrativas: [
          { type: this.rules.errorTypeSelect, label: 'Selecciona un elemento de la lista preaprobada.' }
        ]
      },
      HN: {
        medios: [{ type: this.rules.errorTypeRequired, label: 'Informacin requerida' }],
        investigacionesJudicialesAdministrativas: [
          { type: this.rules.errorTypeSelect, label: 'Selecciona un elemento de la lista preaprobada.' }
        ]
      }
    };
    this.getCatalogo('PAISES');
    this.getContrato('MANIFESTACIONES_AUTORIZACIONES');
    this.getContrato('DECLARACION_FLUJO_INGRESO');
    this.setRules({
      medios: ['', [
        this.rules.validationForRequired()
      ]],
      tieneInvestigacionesJudicialesAdministrativas: [false, [
        this.rules.resetMyCallbackValidation([
          'investigacionesJudicialesAdministrativas'
        ])
      ]] ,
      investigacionesJudicialesAdministrativas: ['', [
        this.rules.validationForSelect(() => this.catalogoData[this.pais].PAISES, (control: FormGroup) => {
          if ( !control.parent ) {
            return true;
          }
          return control.parent.get('tieneInvestigacionesJudicialesAdministrativas').value !== true;
        })
      ]],
      aceptoManifestacionesAuorizaciones: [false, [
        this.rules.validationForRequiredTrue()
      ]],
      declaroObligaciones: [false, [
        this.rules.validationForRequiredTrue()
      ]]
    });
  }

  getCatalogo(KEY: string, pais: string = this.pais): void {
    this.catalogoData[pais][KEY] = [];
    this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.catalogoData[pais][KEY] = response ? response.map( item => Object.assign({value: item.codigo, label: item.nombre}, item)) : [];
    });
  }

  getContrato(KEY: string, pais: string = this.pais): void {
    this.contrato[pais][KEY] = [];
    this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.contrato[pais][KEY] = response[0];
    });
  }

  setValue( item: any): string {
    return item.codigo;
  }

  setLabel( item: any): string {
    return item.nombre;
  }

  filtro( item: any): string {
    return item.nombre;
  }

  async openModal(KEY: string, controlName: string, e?: any): Promise<void> {
    if (!e || e.target.tagName !== 'A') { return; }
    this.textoContrato = this.contrato.PA[KEY].message;
    const modal: Modal = {
      display: true,
      buttons: [
        {
          callback: ( close ) => {
            this.formulario.get(controlName).setValue(true);
            close();
          },
          buttonText: 'Aceptar',
          class: 'alertModal__footer--button alertModal__footer--button--primary'
        }
      ],
      title: this.contrato.PA[KEY].title,
      message: this.textoContrato,
      entryComponent: InfoModalTMAComponent
    };
    this.obser.sendData(modal, MODAL);
  }

}
