import { Component, OnInit, OnDestroy, ElementRef, ViewChild } from '@angular/core';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';
import { Payload, BackButton } from 'src/app/modules/common/components/base/base';
import { ViewEncapsulation } from 'src/app/modules/common/components/encaptulation';
import { MODULO, LENGUAJE, PAIS, IP, MODAL } from 'src/app/modules/common/CONST';
import htmlToImage from 'html-to-image';
import { retry } from 'rxjs/operators';
import { AlertModalTMAComponent } from 'src/app/modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';
import { ThrowStmt } from '@angular/compiler';

@Component({
  selector: 'app-lhc001',
  templateUrl: './lhc001.component.html',
  styleUrls: ['./lhc001.component.scss', '../../../../../assets/cam/scss/components/autorizaciones/_lhc001.scss'],
  encapsulation: ViewEncapsulation.ShadowDom
})
export class Lhc001Component extends BaseComponent implements OnInit, OnDestroy, Payload, BackButton {

  @ViewChild('LHC') private screen: ElementRef<HTMLElement>;
  enCaptura: boolean;
  capturas: Array<{
    nombre: string;
    fecha: string;
    status: boolean;
  }>;

  contrato: {
    message: string;
    fecha: string;
    version: string;
    lhcDoc: string;
  };
  show: boolean;

  ngOnInit() {
    this.show = false;
    this.capturas = [{
      nombre: '',
      fecha: '',
      status: false
    }, {
      nombre: '',
      fecha: '',
      status: false
    }];
    this.enCaptura = false;
    this.getContrato('AUTORIZACION_LHC_SALVADOR');
    this.setRules({
      autorizaciones: [false, [
        this.rules.validationForRequired()
      ]]
    });
  }

  ngOnDestroy(): void {

  }

  finalizar(): void {
    this.obser.sendData(
      {
        display: true,
        buttons: [
          {
            callback: (close) => close(),
            buttonText: 'Regresar',
            class: 'alertModal__footer--button alertModal__footer--button--primary'
          }, {
            callback: (close) => {
              this.postMessagesService.appFinish('', 'op_home');
              this.disableButton = true;
              close();
            },
            buttonText: 'Salir',
            class: 'alertModal__footer--button alertModal__footer--button--auxiliar'
          }
        ],
        title: '',
        message: 'Estimado cliente, no es posible continuar con el proceso por medio de este canal sin su autorizacin.',
        entryComponent: AlertModalTMAComponent
      }, MODAL
    );
  }


  callWorkflow(): void {
    if ( !this.capturas[0].status || !this.capturas[1].status ) {
      return;
    }
    this.onCall(this.getDataToPayload());
  }

  captura(): void {
    this.disableButton = true;
    this.enCaptura = true;
    htmlToImage.toPng(this.screen.nativeElement, {
      backgroundColor: 'white',
      quality: 1
    }).then((dataUrl) => {
      const newPayload = {
        typoDocumento: '',
        numDocumento: '',
        ip: '',
        canal: this.canal,
        modulo: this.modulo,
        pais: this.pais,
        lenguaje: this.lenguaje
      };
      this.enCaptura = false;
      this.onCall(this.getDataToPayload());
    });
  }

  getDataToPayload(data?: any): () =>  ({ [key: string]: any; }) {
    return () => ({
      autorizacionesLhc: {
        check: true,
        fecha: this.contrato.fecha,
        version: this.contrato.version
      },
      fechaEnvioImagenAceptacion: this.capturas[0].fecha,
      nombreImagenAceptacion: this.capturas[0].nombre,
      fechaEnvioImagenConfirmacion: this.capturas[1].fecha,
      nombreImagenConfirmacion: this.capturas[1].nombre,
      nombreHost: this.HOSTNAME,
      sistemaOperativo: this.OS,
    });
  }

  getDataToBackButton(data?: any): () => ({ [key: string]: any; }) {
    // throw new Error('Method not implemented.');
    return () => ({});
  }

  getContrato(KEY: string): void {
    this.catalogo.data(KEY, this.params([PAIS, MODULO, LENGUAJE])).subscribe( response => {
      this.contrato = response[0];
      this.catalogo.assetsTextV2(this.contrato.lhcDoc).subscribe( response2 => {
        this.contrato.lhcDoc = response2;
        setTimeout( () => this.show = true, 2000);
        }
      );
    });
  }

  capturar(n: number, value): void {
    this.enCaptura = true;
    if ( value !== true ) {
      return;
    }
    setTimeout( () => {
      htmlToImage.toPng(this.screen.nativeElement, {
        backgroundColor: 'white',
        quality: 0.1
      }).then( img => {
        const pay = {
          typeDocumento: this.payload.documento.tipo,
          numDocumento: this.payload.documento.numero,
          ip: this.obser.lastValue(IP),
          canal: this.canal,
          modulo: this.modulo,
          pais: this.pais,
          lenguaje: this.lenguaje,
          img_001: img,
          formatoArchivo: 'png',
          numero: `${n + 1}`
        };
        this.uploadImagen(pay, n);
      });
    },  1500);
  }

  uploadImagen(data: any, n: number): void {
    this.saveImagesLHC(data).pipe(retry(3))
    .subscribe( response => {
      this.enCaptura = false;
      this.capturas[n] = {
        fecha: response.data.fechaFtp,
        nombre: response.data.nombreArchivo,
        status: true
      };
      if ( n === 0) {
        this.obser.sendData(
          {
            display: true,
            buttons: [
              {
                callback: (close) => { this.capturar(1, true); close(); },
                buttonText: 'Autorizo',
                class: 'alertModal__footer--button alertModal__footer--button--primary'
              }, {
                callback: (close) => { this.formulario.get('autorizaciones').setValue(false); close(); }, // this.capturar(1, true),
                buttonText: 'No autorizo',
                class: 'alertModal__footer--button alertModal__footer--button--auxiliar'
              }
            ],
            title: '',
            message: 'Autoriz las condiciones ledas en el presente documento?',
            entryComponent: AlertModalTMAComponent
          }, MODAL
        );
      }
    }, err => {
      this.enCaptura = false;
      this.obser.sendData(
        {
          display: true,
          buttons: [
            {
              callback: (close) => close(),
              buttonText: 'Finalizar',
              class: 'alertModal__footer--button alertModal__footer--button--primary'
            }
          ],
          title: 'Lo sentimos',
          message: 'En este momento no podemos atender su solicitud, por favor intente ms tarde.',
          entryComponent: AlertModalTMAComponent
        }, MODAL
      );
    });
  }
}
