import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Aut001Component } from './aut001/aut001.component';
import { Aut002Component } from './aut002/aut002.component';
import { Lhc001Component } from './lhc001/lhc001.component';
import { SecurityAccessGuard } from '../../common/guards/security-access.guard';
import { Aut003Component } from './aut003/aut003.component';

const routes: Routes = [
  {
    path: 'aut001',
    component: Aut001Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'AUT001'
    }
  },
  {
    path: 'aut002',
    component: Aut002Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'AUT002'
    }
  },
  {
    path: 'aut003',
    component: Aut003Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE011'
    }
  },
  {
    path: 'lhc001',
    component: Lhc001Component,
    canActivate: [SecurityAccessGuard],
    canDeactivate: [SecurityAccessGuard],
    data: {
      stepId: 'CRE016'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AutorizacionesRoutingModule { }
